.imploding-puppies-game .card, .imploding-puppies-game .card-deck {
    border-radius: 5px;
    border: 4px solid;
    display: inline-block;
    height: 140px;
    margin: 10px;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 100px;
    -webkit-transform-origin: 50% 250%;
        -ms-transform-origin: 50% 250%;
            transform-origin: 50% 250%;
    background: #fff;
    cursor: pointer;
}

.imploding-puppies-game .card:before, .imploding-puppies-game .card-deck:before {
    background-color: rgba(153, 153, 153, 0.2);
    border: 1px solid #999;
    content: '';
    display: block;
    height: 85%;
    margin: 10%;
    width: 80%;
}

.imploding-puppies-game .card-deck span {
    position: absolute;
    font-size: 50px;
    margin-top: 50px;
    text-shadow: 4px 4px black;
    color: white;
    background: rgba(100,100,100,0.4);
    padding: 0 15px;
    pointer-events: none;
    z-index: 1;
    top: -10px;
    left: 8px;
    width: 56px;
    text-align: center;
}

.imploding-puppies-game .card.face-down:before {
    background-image: -webkit-linear-gradient(45deg, #808080 25%, transparent 25%), -webkit-linear-gradient(135deg, #808080 25%, transparent 25%), -webkit-linear-gradient(45deg, transparent 75%, #808080 75%), -webkit-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: -o-linear-gradient(45deg, #808080 25%, transparent 25%), -o-linear-gradient(135deg, #808080 25%, transparent 25%), -o-linear-gradient(45deg, transparent 75%, #808080 75%), -o-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: linear-gradient(45deg, #808080 25%, transparent 25%), linear-gradient(-45deg, #808080 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #808080 75%), linear-gradient(-45deg, transparent 75%, #808080 75%);
    background-size: 20px 20px;
}

.imploding-puppies-game .card-value {
    position: absolute;
    top: 15px;
    text-align: center;
    left: 5px;
    right: 5px;
    white-space: normal;
}

.imploding-puppies-game .card-face {
    -ms-flex-align: center;
        align-items: center;
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    font-size: 60px;
    -ms-flex-pack: center;
        justify-content: center;
    left: 0;
    position: absolute;
    right: 0;
    top: 40px;
}

.imploding-puppies-game .card.interactive:not(.disabled):hover {
    z-index: 1;
    top: -10px;
    cursor: pointer;
}

.imploding-puppies-game .card.selected {
    -webkit-box-shadow: 0 0 5px;
            box-shadow: 0 0 5px;
    top: -50px!important;
}

.imploding-puppies-game .card.disabled {
    background: #eee;
}

.nope {
    background: red;
}



