.canvas-parent {
    margin-top: 10px;
    width: 100%;
    height: calc(95vh - 100px);
    line-height: 0;
    position: relative;
}

.touchdecider-canvas {
    cursor: pointer;
    border: 1px solid #ccc;
}

.touchdecider-message {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -ms-touch-action: none;
        touch-action: none;
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    line-height: 1;
    font-size: 30px;
    text-align: center;
}



