.card-deck-table .card {
    border-radius: 5px;
    border: 1px solid black;
    display: inline-block;
    height: 140px;
    margin: 10px;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 100px;
}

.card-deck-table .card:before {
    background-color: rgba(153, 153, 153, 0.2);
    border: 1px solid #999;
    content: '';
    display: block;
    height: 85%;
    margin: 10%;
    width: 80%;
}

.card-deck-table .card-deck:before {
    background-image: -webkit-linear-gradient(45deg, #808080 25%, transparent 25%), -webkit-linear-gradient(135deg, #808080 25%, transparent 25%), -webkit-linear-gradient(45deg, transparent 75%, #808080 75%), -webkit-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: -o-linear-gradient(45deg, #808080 25%, transparent 25%), -o-linear-gradient(135deg, #808080 25%, transparent 25%), -o-linear-gradient(45deg, transparent 75%, #808080 75%), -o-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: linear-gradient(45deg, #808080 25%, transparent 25%), linear-gradient(-45deg, #808080 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #808080 75%), linear-gradient(-45deg, transparent 75%, #808080 75%);
    background-size: 20px 20px;
}

.card-deck-table .card.red {
    color: rgb(201, 31, 31);
}

.card-deck-table .card-value {
    position: absolute;
}

.card-deck-table .card-value-left {
    top: 15px;
    left: 15px;
}

.card-deck-table .card-value-right {
    bottom: 15px;
    right: 15px;
}

.card-deck-table .card-face {
    -ms-flex-align: center;
        align-items: center;
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    font-size: 100px;
    -ms-flex-pack: center;
        justify-content: center;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
}

.card-deck-table .buttons {
    display: inline-block;
    vertical-align: top;
}

.card-deck-table .buttons>button {
    display: block;
    margin: 10px;
}



