.container {
    border-top: 3px solid #103D5D;
    margin-right: 50px;
    background-color: rgb(237, 237, 237);
    padding: 30px 20px;
    -webkit-box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .14), 0 3px 3px -2px rgba(0, 0, 0, .12), 0 1px 8px 0 rgba(0, 0, 0, .2);
            box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .14), 0 3px 3px -2px rgba(0, 0, 0, .12), 0 1px 8px 0 rgba(0, 0, 0, .2);
    margin: 10px 0 30px 0;
}

.container.collapsable {
    padding: 0;
}

.container.collapsable .content {
    padding: 30px 20px;
}

.container .collapsed-header {
    background: #103D5D;
    color: white;
    cursor: pointer;
    margin: 0;
    width: 100%;
}

.container .collapsed-header .container-button {
    background: rgb(76, 109, 128);
    display: inline-block;
    margin-right: 10px;
    padding: 5px;
}

/** Hoverable styles */
.container .collapsed-header.hover {
    height: 0px;
    -webkit-transition: height 0.2s;
    -o-transition: height 0.2s;
    transition: height 0.2s;
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
}

.container .collapsed-header.hover .container-button {
    -webkit-transform: scaleY(0);
        -ms-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    -o-transition: transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
    -webkit-transform-origin: 0 0;
        -ms-transform-origin: 0 0;
            transform-origin: 0 0;
}

.container .collapsed-header.hover span {
    display: none;
}

.container:hover .collapsed-header.hover {
    height: 41px;
}

.container:hover .collapsed-header.hover .container-button {
    -webkit-transform: scaleY(1);
        -ms-transform: scaleY(1);
            transform: scaleY(1);
}

.container:hover .collapsed-header.hover span {
    display: inline;
}

/** Error styles */
.container.error {
    border-top: 3px solid rgb(153, 25, 25);
}

.container.error .collapsed-header {
    background: rgb(153, 25, 25);
}

.container.error .collapsed-header .container-button {
    background: rgb(146, 76, 76);
}



