.imploding-puppies-game {
    width: 100%;
    height: 100%;
    position: relative;
    margin: 40px 0;
}

.imploding-puppies-game .card-deck:before {
    background-image: -webkit-linear-gradient(45deg, #808080 25%, transparent 25%), -webkit-linear-gradient(135deg, #808080 25%, transparent 25%), -webkit-linear-gradient(45deg, transparent 75%, #808080 75%), -webkit-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: -o-linear-gradient(45deg, #808080 25%, transparent 25%), -o-linear-gradient(135deg, #808080 25%, transparent 25%), -o-linear-gradient(45deg, transparent 75%, #808080 75%), -o-linear-gradient(135deg, transparent 75%, #808080 75%);
    background-image: linear-gradient(45deg, #808080 25%, transparent 25%), linear-gradient(-45deg, #808080 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #808080 75%), linear-gradient(-45deg, transparent 75%, #808080 75%);
    background-size: 20px 20px;
}

.imploding-puppies-deck-holder {
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%;
    position: absolute;
    bottom: 290px;
    white-space: nowrap;
    z-index: 10;
}

.imploding-puppies-deck-holder.small {
    -webkit-transform: scale(0.5) translate(-100%, 110px);
        -ms-transform: scale(0.5) translate(-100%, 110px);
            transform: scale(0.5) translate(-100%, 110px);
}

/** Discard pile */

.imploding-puppies-discard {
    position: relative;
    display: inline-block;
    margin-left: 20px;
}

.imploding-puppies-discard .discard-placeholder {
    border: 4px dashed #999;
    border-radius: 5px;
    display: inline-block;
    height: 140px;
    margin: 10px;
    position: relative;
    width: 100px;
}

.imploding-puppies-discard>.card {
    -webkit-transform-origin: center!important;
        -ms-transform-origin: center!important;
            transform-origin: center!important;
    position: absolute!important;
    top: -20px;
    left: 0;
    -webkit-transition: -webkit-transform 0.5s ease-out;
    transition: -webkit-transform 0.5s ease-out;
    -o-transition: transform 0.5s ease-out;
    transition: transform 0.5s ease-out;
    transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
    pointer-events: none;
}

.imploding-puppies-game .remote-area {
    height: 400px;
    position: relative;
    z-index: 1;
}

.imploding-puppies-game .player-hightlight {
    position: absolute;
    top: 220px;
    left: calc(50% - 100px);
    width: 200px;
    height: 200px;
    border-radius: 50%;
    opacity: 1;
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    -o-transition: transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.imploding-puppies-game .player-hightlight.small {
    width: 200px;
    height: 100px;
    left: calc(50% - 75px);
    top: 235px;
    border-radius: 20px;
}

.imploding-puppies-game .active-player-highlight {
    background: rgba(230, 230, 0, 0.5);
}

.imploding-puppies-game .target-player-highlight {
    background: rgba(230, 73, 0, 0.5);
    -webkit-animation: fade-target 2s linear 1 forwards;
            animation: fade-target 2s linear 1 forwards;
}

.imploding-puppies-game .target-player-highlight.small {
    -webkit-animation: fade-small-target 2s linear 1 forwards;
            animation: fade-small-target 2s linear 1 forwards;
}

@-webkit-keyframes fade-target {
    20% {
        opacity: 0.4;
    }
    40% {
        opacity: 1;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 1;
        width: 200px;
        height: 200px;
        top: 220px;
        left: calc(50% - 100px);
    }
    100% {
        width: 300px;
        height: 300px;
        top: 170px;
        left: calc(50% - 150px);
        opacity: 0;
    }
}

@keyframes fade-target {
    20% {
        opacity: 0.4;
    }
    40% {
        opacity: 1;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 1;
        width: 200px;
        height: 200px;
        top: 220px;
        left: calc(50% - 100px);
    }
    100% {
        width: 300px;
        height: 300px;
        top: 170px;
        left: calc(50% - 150px);
        opacity: 0;
    }
}

@-webkit-keyframes fade-small-target {
    20% {
        opacity: 0.4;
    }
    40% {
        opacity: 1;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 1;
        width: 200px;
        height: 100px;
        left: calc(50% - 75px);
        top: 235px;
    }
    100% {
        width: 300px;
        height: 200px;
        left: calc(50% - 125px);
        top: 175px;
        opacity: 0;
    }
}

@keyframes fade-small-target {
    20% {
        opacity: 0.4;
    }
    40% {
        opacity: 1;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 1;
        width: 200px;
        height: 100px;
        left: calc(50% - 75px);
        top: 235px;
    }
    100% {
        width: 300px;
        height: 200px;
        left: calc(50% - 125px);
        top: 175px;
        opacity: 0;
    }
}



